/* Button Style */
.btnsec {
  padding: 50px;
}

.btn {
  background: rgb(0, 88, 88);
  display: inline-block;
  padding: 0;
  border: none;
  cursor: pointer;
  outline-offset: 4px;
  border-radius: 12px;
}

.front {
  display: block;
  padding: 12px 42px;
  border-radius: 12px;
  font-size: 1.25em;
  background: teal;
  color: white;
  transform: translateY(-6px);
}

/* Active Button State */
.button:active {
  background-color: rgb(236, 89, 148);
}

/* responsive media */
@media all and (max-width: 30em) {
  .button {
    display: block;
    margin: 0.4em auto;
  }
}
.navigation {
  background-color: #3a3153;
  width: 100%;
  z-index: 2;
  position: relative;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

ul {
  display: flex;
}

li {
  list-style-type: none;
  padding: 10px;
}

.nav-link {
  color: #FFFDFA;
  font-size: 1.5rem;
  text-decoration: none;
}

#logo {
  font-size: 3rem;
}

.brand {
  padding-bottom: 10px;
}

.hero {
  padding-bottom: 300px;
}

/* @import '/src/*'; */
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: "Fira Sans", sans-serif;
}

/* Nav */
.nav-list {
  padding-right: 3rem;
}

.brand {
  padding-left: 3rem;
}

/* Hero */
.hero {
  margin-top: -300px;
  background-size: cover;
  background-position: center;
  background-image: url(../../images/deadline.jpg);
  height: 100vh;
  position: relative;
  z-index: 1;
}

